@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h1 {
    @apply font-rubik text-secondary flex justify-center font-extrabold text-shadow-lg text-5xl;
  }

  h2 {
    @apply font-rubik text-secondary font-bold text-shadow-md text-4xl;
  }

  h3 {
    @apply font-rubik text-secondary text-shadow-md text-3xl;
  }

  ul {
    list-style-type: square;
  }

  ul li {
    @apply relative py-2 text-justify lg:pr-6;
  }

  ul li::marker {
    @apply text-xl text-secondary;
  }
}

html {
  font-size: 16px;
}
